 .b-a, .bar, .bar:before, .bar:after {
	 transition: all 0.25s;
	 content: "";
	 position: absolute;
	 left: 0;
	 height: 2px;
	 width: 30px;
	 border-radius: 15px;
	 background-color: rgba(255, 255, 255, 0.95);
}
 .menu-collapsed {
    display: block;
	 transition: all 0.25s;
	 position: fixed;
	 top: 10vh;
	 left: 9px;
	 height: 36px;
	 width: 36px;
	 z-index: 1;
	 cursor: pointer;
}
 .menu-collapsed ul {
	 transition: all 0s;
	 position: fixed;
	 left: -9000px;
     text-decoration: none;
}
 .bar {
	 position: fixed;
	 left: 85vw;
	 top: 30px;
	 z-index: 99999999999;
}
